@use "../general/util";

img {
    max-width: 100%;
    height: auto;
    object-fit: fill;
}

.avatar {
    border-radius: 50%;
}

.icon {
    @extend %flex-center;
    width: 33.33%;
    
    img {
        max-width: 32px;
    }
}

.proj-image {
    border-radius: inherit;
    justify-content: center;
    align-items: center;

    img {
        border-radius: inherit;
        border-bottom-left-radius: 0;
        border-bottom-right-radius: 0;
    }
}

.flex-image {
    @extend %flex-center;
    justify-content: center;
}

@media (max-width: 320px) {
    .avatar {
        width: 100px;
    }
}

@media screen and (min-width: 768px) {
    .proj-image {
        border-radius: 5px;
        img {
            border-bottom-left-radius: 5px;
            border-bottom-right-radius: 5px;
        }
    }
}