@mixin animation($animation, $time) {
    animation-name: $animation;
    animation-duration: $time;
}

@keyframes fadeIn {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

@keyframes fadeInUp {
    from {
        transform: translate3d(0, 5%, 0);
        opacity: 0;
    }
    to {
        transform: translate3d(0, 0, 0);
        opacity: 1;
    }
}

@keyframes fadeInDown {
    from {
        transform: translate3d(0, -5%, 0);
        opacity: 0;
    }
    to {
        transform: translate3d(0, 0, 0);
        opacity: 1;
    }
}

@keyframes fadeOut {
    from {
        opacity: 1;
    }
    to {
        opacity: 0;
    }
}

.transition-menu {
    @include animation(fadeIn, 1s);
}

.transition-page {
    @include animation(fadeInUp, 1.5s);
}

.transition-work-content {
    @include animation(fadeIn, 0.5s);
}